import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  dialogues: [
    {
      id: 1,
      name: 'Denis',
      avatar:
        'data:image/png;base64,',
    },
    {
      id: 2,
      name: 'Julia',
      avatar:
        'https://image.freepik.com/free-vector/avatar-of-a-girl-cute-female-image-ordinary-girl-fashionable-flat-style_15870-697.jpg',
    },
    {
      id: 3,
      name: 'Lutsius',
      avatar:
        'https://cs4.pikabu.ru/images/big_size_comm/2015-05_1/14308017229534.jpg',
    },
    {
      id: 4,
      name: 'Pasha',
      avatar:
        'https://www.meme-arsenal.com/memes/a5dd2f55b36488a10172f4f84352846b.jpg',
    },
  ],
  messages: [
    { id: 1, message: "Hi, I've created this site" },
    { id: 2, message: 'Hello, I am a photographer' },
    { id: 3, message: 'Meow, I am a cat rock climber' },
    { id: 4, message: 'Hey, peaches! I am the creator of VK!' },
  ],
  newMessage: '',
}

const dilaogueSlice = createSlice({
  name: 'dialogue',
  initialState,
  reducers: {
    addNewMessage: (state) => {
      if (state.newMessage) {
        const newPostMessage = {
          id: state.messages.length + 1,
          message: state.newMessage,
        }

        state.messages.push(newPostMessage)
        state.newMessage = ''
      }
    },
    updateNewPostMessage: (state, action) => {
      state.newMessage = action.payload
    },
  },
})
export const { addNewMessage, updateNewPostMessage } = dilaogueSlice.actions
export default dilaogueSlice.reducer
